@import '../vars';

.card {
    display: grid;    
    user-select: none;
    overflow: hidden;

    margin-bottom: 2em;
    
    em {
        font-size: 1.1em;
        margin-right: 0.05em;
    }

    background: linear-gradient(to top, change-color($cd-blue-1000, $alpha: 0.015), $cd-white 20%);
    
    @media only screen and (max-width: 1000px) {
        grid:
            "name" 3.5em
            "content"
            "space" 1em
            "images" 1fr
            / 1fr;
        
        box-shadow: 0em 0.5em 1em $cd-grey;

        .title-container {
            grid-area: name;
            font-size: 2.5em;
            margin-bottom: 1em;
            

            align-self: start;
            justify-self: start;
            
            width: 0;
            position: relative;

            .title {
                position: absolute;
                left: -.5em;
                top: -.7em;
                h1 {
                    margin: 0;
                    text-transform: uppercase;
                    white-space: nowrap;
                }

                
                &:last-child {
                    display: none;
                }
            }
        }
    }

    
    @media only screen and (min-width: 1000px) {
        grid:
            "content images" 4em
            "name images" auto
            / auto auto;
        column-gap: 1em;
        max-width: 70em;

        
        .title-container {
            grid-area: name;
            font-size: 4.5em;
            margin-top: 1em;

            align-self: end;
            justify-self: start;
            
            width: 0;

            position: relative;

            .title {
                position: absolute;
                left: -.4em;
                bottom: -.54em;
                h1 {
                    margin: 0;
                    text-transform: uppercase;
                    white-space: nowrap;
                }

                &:first-child {
                    color: #eaeaea;
                }

                &:last-child {
                    color: $cd-blue-500;
                    mix-blend-mode: color-burn;
                    z-index: 3;
                }
            }
        }
    }



    .content {
        grid-area: content;
        margin-left: 1em;
    }

    .images {
        grid-area: images;

        @media only screen and (max-width: 1000px) {
            place-self: stretch stretch;        
        }

        @media only screen and (min-width: 1000px) {
            place-self: stretch end;   

            width: 25em;
            height: 25em;
        }

        z-index: 1;

    }

    $no-shadow-color: change-color($cd-grey, $alpha: 0);
    box-shadow: 0em 0 0 $no-shadow-color;
    transition: box-shadow .3s;
    &:hover {
        box-shadow: 0em 0.5em 1em $cd-grey;
        cursor: pointer;
    }
}


