.content-container{display:flex;align-items:center;justify-content:center}.content{position:relative;flex:1 1 auto;max-width:90%;display:flex;flex-direction:column;align-items:center}

.card{display:grid;-webkit-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden;margin-bottom:2em;background:linear-gradient(to top, rgba(9,20,45,0.015), #FDFDFF 20%);box-shadow:0em 0 0 rgba(214,214,214,0);transition:box-shadow .3s}.card em{font-size:1.1em;margin-right:0.05em}@media only screen and (max-width: 1000px){.card{grid:"name" 3.5em "content" "space" 1em "images" 1fr / 1fr;box-shadow:0em 0.5em 1em #D6D6D6}.card .title-container{grid-area:name;font-size:2.5em;margin-bottom:1em;align-self:start;justify-self:start;width:0;position:relative}.card .title-container .title{position:absolute;left:-.5em;top:-.7em}.card .title-container .title h1{margin:0;text-transform:uppercase;white-space:nowrap}.card .title-container .title:last-child{display:none}}@media only screen and (min-width: 1000px){.card{grid:"content images" 4em "name images" auto / auto auto;grid-column-gap:1em;-webkit-column-gap:1em;column-gap:1em;max-width:70em}.card .title-container{grid-area:name;font-size:4.5em;margin-top:1em;align-self:end;justify-self:start;width:0;position:relative}.card .title-container .title{position:absolute;left:-.4em;bottom:-.54em}.card .title-container .title h1{margin:0;text-transform:uppercase;white-space:nowrap}.card .title-container .title:first-child{color:#eaeaea}.card .title-container .title:last-child{color:#5086bc;mix-blend-mode:color-burn;z-index:3}}.card .content{grid-area:content;margin-left:1em}.card .images{grid-area:images;z-index:1}@media only screen and (max-width: 1000px){.card .images{place-self:stretch stretch}}@media only screen and (min-width: 1000px){.card .images{place-self:stretch end;width:25em;height:25em}}.card:hover{box-shadow:0em 0.5em 1em #D6D6D6;cursor:pointer}

.stripes{position:absolute;left:0;top:0;bottom:0;display:flex}.stripes .s1,.stripes .s2,.stripes .s3{box-shadow:0 0 0.4em #09142D;transition:flex 0.1s;flex:0 0 1.5em}.stripes .s1:hover,.stripes .s2:hover,.stripes .s3:hover{flex:0 0 2em}.stripes .s1{background-color:#7AADD6;z-index:1}.stripes .s2{background-color:#3A68A1;z-index:2}.stripes .s3{background-color:#19366D;z-index:3}.header-container{position:relative;overflow:hidden;max-width:100%;width:100%}@media only screen and (max-width: 800px){.header-container{margin-bottom:2em}}@media only screen and (min-width: 800px){.header-container{margin-bottom:5em}}.header{margin-left:6em;margin-right:1em;display:grid;flex:1 0 auto;max-width:100%}@media only screen and (max-width: 600px){.header{grid-template:"name" "email" "twitter" / auto}.header p{margin-top:0}}@media only screen and (min-width: 600px){.header{grid-template:"name email" "name twitter" / auto auto}.header p{margin-top:default}}@media only screen and (min-width: 800px){.header{grid-template:"name email twitter" / auto auto auto}.header p{margin-top:default}}.header *{justify-self:left;align-self:center}.header h1 a{text-decoration:none;color:#000008}.header h1 a :link{color:#000008}.header h1 a :visited{color:#000008}@media (prefers-color-scheme: dark){.header h1 a{color:#FDFDFF}.header h1 a :link{color:#FDFDFF}.header h1 a :visited{color:#FDFDFF}}

html,body,#root{width:100vw;margin:0;overflow-x:hidden}h1{font-family:Inter, sans-serif;font-weight:300;font-size:2.25em}h2{font-family:Inter, sans-serif;font-weight:300;font-size:2em}h3{font-family:Inter, sans-serif;font-weight:700;font-size:1.75em}h4{font-family:Inter, sans-serif;font-weight:700;font-size:1.5em}h5{font-family:Inter, sans-serif;font-weight:700;font-size:1.25em}h6{font-family:Inter, sans-serif;font-weight:700;font-size:1em;text-transform:uppercase}p{font-family:Nunito, sans-serif;font-size:1.25em}img{max-width:100%;display:block}html{color:#000008;background-color:#FDFDFF}html a:link{color:#284E87}html a:visited{color:#0F255A}html a:hover,html a:active{color:#5086bc}html ::selection{background-color:#BFE4F8}

